import { IProductAccessoriesListProps } from "../../interfaces/accessories.interface"

const ProductAccessoriesList = (item: IProductAccessoriesListProps) => {
  return (
    <>
      <dt>/</dt>
      <dd>{item.model}</dd>
      <dt></dt>
      <dd>{item.sex}</dd>
      <dt></dt>
      <dd>{item.brand}</dd>
      <dt></dt>
      <dd>{item.material}</dd>
      <dt></dt>
      <dd>{item.color}</dd>
      { item.dimensions &&  (
        <>
        <dt> </dt>
        <dd>{item.dimensions}</dd>
        </>
      )}
    </>
  )
}

export default ProductAccessoriesList